import PropTypes from 'prop-types'
import React, { useEffect, useState, createRef } from 'react'
import classNames from 'classnames'
import {
  CCol,
  CCard,
  CCardHeader,
  CCardBody,
  CCardTitle,
  CCardText,
  CButton,
  CCardFooter,
} from '@coreui/react'
import { rgbToHex } from '@coreui/utils'

const ThemeView = () => {
  const [color, setColor] = useState('rgb(255, 255, 255)')
  const ref = createRef()

  useEffect(() => {
    const el = ref.current.parentNode.firstChild
    const varColor = window.getComputedStyle(el).getPropertyValue('background-color')
    setColor(varColor)
  }, [ref])

  return (
    <table className="table w-100" ref={ref}>
      <tbody>
        <tr>
          <td className="text-medium-emphasis">HEX:</td>
          <td className="font-weight-bold">{rgbToHex(color)}</td>
        </tr>
        <tr>
          <td className="text-medium-emphasis">RGB:</td>
          <td className="font-weight-bold">{color}</td>
        </tr>
      </tbody>
    </table>
  )
}

const ThemeColor = ({ className, children }) => {
  const classes = classNames(className, 'theme-color w-75 rounded mb-3')
  return (
    <CCol xs={12} sm={6} md={4} xl={2} className="mb-4">
      <div className={classes} style={{ paddingTop: '75%' }}></div>
      {children}
      <ThemeView />
    </CCol>
  )
}

ThemeColor.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
}

const DownloadSlips = () => {
  return (
    <>
      <CCard className="text-center">
        <CCardHeader>Download Slip</CCardHeader>
        <CCardBody>
          <CCardTitle>
            JOB OPPORTUNITIES IN SINDH POLICE (Mirpurkhas Range) for Constable BS-07
          </CCardTitle>
          <CCardText>
            Applications are invited from the candidates having domicile of District concerned
            against the following vacant posts of Police Constable (BS-07).
          </CCardText>
          <CCardText>Project posted on: 14-04-2023</CCardText>
          {/* <div>
            <CPopover title="Note" content="Project will be close on the time." placement="bottom">
              <CButton color="danger">Project Close Date:12-05-2023</CButton>
            </CPopover>
          </div> */}
          <CButton href="#" style={{ marginLeft: '10px' }}>
            View Post
          </CButton>
          <CButton href="#" style={{ marginLeft: '10px' }}>
            Download Advertisement
          </CButton>
        </CCardBody>
        <CCardFooter className="text-medium-emphasis">2 days ago</CCardFooter>
      </CCard>
    </>
  )
}

export default DownloadSlips
